%square {
    width: 50px;
    height: 50px;
    border: 5px solid;
    margin-bottom: 30px;
}

%indicator {
    width: 100%;
    height: 50px;
    border: 5px solid red;
    background-color: white;
    margin-bottom: 30px;
}

%line {
    height: 100%;
    background-color: red;
}